footer {
    width: 100%; 
    height: 35vh;
    background-image: none;
    background-size: 100% 100%; 
    background-repeat: repeat;
    background-attachment: scroll;
    background-position: 0% 0%;
    position: relative; 
    bottom: 0pt;
    left: 0pt;
    margin: auto;
    overflow: visible;
}

.foot {
    float: left;
    width: 33.33%;
    background-color: #282c34;
    color: white;
    height: 20vh; 
    overflow: visible;
}

.foot li {
    text-decoration: none;
    list-style-type: none;
    padding: 0px 0px;
    color: white;
}

a {
    color: #61dafb;
}

h3 {
    font-size: 3em;
}