ul {
    display: inline-block;
    margin: 0;
    padding: 0;
}

li {
    display: inline;
    float: left;
    text-decoration: none;
    list-style-type: none;
    padding: 0px 2px;
    color: white;
}

a {
    color: #61dafb;
    display: block;
    padding: 3em;
}

li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }

  li a:hover {
    background-color: #111;
  }